.btn {
  text-align: center;
  cursor: pointer;
  color: #fff;
  width: 150px;
  height: 40px;
  font-family: inherit;
  line-height: 40px;
  font-size: inherit;
  background: none;
  border: none;
  outline: none;
  position: relative;
}

.btn-one {
  z-index: 0;
  transition: all .3s;
  position: relative;
}

.btn-one span {
  z-index: 2;
  transition: all .3s;
  position: relative;
}

.btn-one:before {
  content: "";
  z-index: 1;
  opacity: 0;
  border-top: 1px solid #ffffff80;
  border-bottom: 1px solid #ffffff80;
  width: 100%;
  height: 100%;
  transition: all .3s;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scale(.1, 1);
}

.btn-one:hover span {
  letter-spacing: 2px;
}

.btn-one:hover:before {
  opacity: 1;
  transform: scale(1);
}

.btn-one:after {
  content: "";
  z-index: 1;
  background-color: #ffffff1a;
  width: 100%;
  height: 100%;
  transition: all .3s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.btn-one:hover:after {
  opacity: 0;
  transform: scale(.1, 1);
}

